
import React, { useEffect, useState, useRef } from "react";
import { getStore, gen16CodeId, ObjectKeys, callDispatchEvent, fetchPostH, getUrlServerSide, updStore } from "../../../stores/Utils";

import {SearchInput_Cmpt } from "../../../components";
import "./dashboard.css"
import { CloseModal } from "../../../components/DialogHRM";


const SearchInput = SearchInput_Cmpt();




const PickAccount  = ({ data, elmId, handleClick, close }) => {

	const { modalID } = data;
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);
	const [loading, set_loading] = useState(false);


	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			
			set_obs(gen16CodeId())
		}
    });


    
	const search_account = async (f, v) => {
		set_loading(true)
		let bdyq2 = {
			query: "getScanAccountNotary",
			params: {
				limit: 250,
				":search": getStore("searchPckQry")
			},
			queryString: "!* contain :search",
		};
		const res = fetchPostH(getUrlServerSide(), bdyq2);
		const td = await res;
		//console.log(JSON.stringify(td));
		if (td && td.data) {
			updStore("account_list_2pck", td.data);
			set_obs(gen16CodeId())
		}
		setTimeout(()=>set_loading(false),50);
	};


    

	const handleChange =  (v) => {
		!loading && set_loading(true)
    }
    
    const selectItem =  (v) => {
        handleClick(v);
        CloseModal({id:modalID})
	}

    return (
        <div className="pickBx" 
        {...(modalID ? { "dialog-key-id": modalID } : "")}
        >
            <div className="header">
                <div className="_dsplFlx ">
                    <p className="form_title">
                        Escoje un accounte
                    </p>
                    <div className="flexSpace"/>
                   
                </div>
                <SearchInput
					label={"Buscar un accounte"} 
					width={360}
					obs={set_obs}
					fetchData={search_account}
                    change={handleChange}
                    keyFlds={"searchPckQry"}
				/>
                <div className={`bottom_loading ${loading?"animate":""}`} />
            </div>

            <div>
            <div className="list_pick_scroll">
				{getStore("searchPckQry")?.length>0 && getStore("account_list_2pck") && ObjectKeys(getStore("account_list_2pck"))?.map(cid=>{
					return (
						<AccountItem 
							data={getStore("account_list_2pck")[cid]} 
							elmId={cid} 
                            key={cid}
                            handleClick={selectItem}
						/>
					)
				})}
			</div>
            </div>
        </div>
    )   
}

export default PickAccount;




const AccountItem  = ({ data, elmId, handleClick }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			set_obs(gen16CodeId())
		}
	});

   

	return (
		<div className="account_item_pck" onClick={()=>handleClick(data)}>
			<div className="_dsplFlx ">
				<div className="name">
					{`${data?.firstName} ${data?.lastName}`}
				</div>
			</div>
		</div>
	)
}